body{
    background-color: black;
}
.portada{
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: center;
}
#campeones {
    display: grid;
    padding: 10px;
    gap: 10px;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}
#boton {
    width:100%;
    padding: 10px 20px; 
    font-family: 'Permanent Marker'; 
    font-size: 1.5rem; 
    color: white; 
    background-color: #1dff098e; 
    border: none; 
    border-radius: 5px; 
    cursor: pointer; 
    transition: background-color 0.3s, transform 0.3s; 
}

#boton:hover {
    background-color: #16c68f; 
    transform: scale(1.05); 
}

.carta {
    margin: 10px;
    background-color: #fff;
    text-align: center;
    border-radius: 5px;
    background-color: rgba(61, 61, 61, 0.288);
    max-width: 250px; 
    overflow: hidden; 
    
    
}
.carta img {
    width: 100%;
    height: auto;
    transition: filter 0.3s ease; 
    filter: brightness(1.1) saturate(1.2) blur(2px)}
.carta img:hover {
    filter: brightness(1.3) saturate(1.5) blur(0); 
}


.nombre{
    color: #1dff098e;
    font-family: 'Permanent Marker'; 
}

img{
    width: 100%;
    height: auto;
    
}

.splash-fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    opacity: 0;
    animation: fadeIn 0.5s forwards;
}
.splash-fullscreen img {
    max-width: 90%;
    max-height: 90%;
    border-radius: 10px;
    filter: brightness(1.1) saturate(1.3); 
}
.titulo-campeon {
    color: white; 
    font-family: 'Permanent Marker'; 
    font-size: 2rem; 
    margin: 20px; 
    text-align: center; 
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); 
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}



